//     
window.showAlert = (type, message) => {
	//         
	const alertClass = type === 'success' ? 'alert--success' : 'alert--error'
	const iconSrc = type === 'success' ? 'img/icons/success.svg' : 'img/icons/error.svg'
	const iconClass = type === 'success' ? 'image-success' : 'image-error'

	//   
	const alert = document.createElement('div')
	alert.className = `alert ${alertClass}`

	alert.innerHTML = `
			<div class="alert-content">
					<img loading="lazy" src="${iconSrc}" class="image ${iconClass}" width="24" height="24" alt="">
					<p>${message}</p>
			</div>
			<button type="button" class="btn btn-close">
					<span class="icon">
							<svg>
									<use xlink:href="img/icons/close.svg#svg-close"></use>
							</svg>
					</span>
			</button>
	`

	//    
	const container = document.querySelector('.alert-container')
	container.appendChild(alert)

	setTimeout(() => {
		alert.style.transform = 'translateX(0vw)' //  
	}, 10)

	setTimeout(() => {
		alert.style.transform = '' //  
		setTimeout(() => {
			alert.remove() //   DOM  400 
		}, 400)
	}, 3000)

	//    
	alert.querySelector('.btn-close').onclick = function () {
		alert.style.transform = '' //  
		setTimeout(() => {
			alert.remove() //   DOM  400 
		}, 400)
	}
}